<template>
    <Table :columns="columns" :data="data"></Table>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '',
                        key: 'trade_no'
                    },
                    {
                        title: '',
                        key: 'recharge_amount'
                    },
                    {
                        title: '',
                        key: 'recharge_way'
                    },
                    {
                        title: '',
                        key: 'timestamp'
                    },
                    {
                        title: '',
                        key: 'trade_status'
                    }
                ],
                data: [
                ]
            }
        },
        mounted() {
            axios.get("http://"+location.hostname+"/home/getOrderByUser").then(data => {
                if(data.data.code==1) {
                    this.data = data.data.data.map(item => Object.assign(item,{
                        trade_status: item.trade_status ? "" : ""
                    }));
                } else {
                    console.error(data.data.msg);
                }
             });
        }
    }
</script>